(function() {
  'use strict';

  angular
    .module('geckoboard')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController(cryptoService, fixerFxService, krakenService, $filter, $timeout) {
    var vm = this;
    var audExchangeRate = 1.35;
    var deposit = 60000;
    
    vm.sum = 0;

    fixerFxService.getRate('USD', 'AUD').then(function(rate) {
      audExchangeRate = rate;
    });

    // //After May 12 transactions
    vm.currencies = {
      BTC: {
        quantity: 4.39939,
        name: 'Bitcoin',
        krakenKey: 'BTC',
        fractionSize: 2
      },
      ETH: {
        quantity: 92.25704,
        name: 'Ethereum',
        krakenKey: 'ETH',
        fractionSize: 2
      },
      XRP: {
        quantity: 52094.54242,
        name: 'Ripple',
        krakenKey: 'XRP',
        fractionSize: 4
      },
      LTC: {
        quantity: 295.08033,
        name: 'Litecoin',
        krakenKey: 'LTC',
        fractionSize: 2
      },
      DASH: {
        quantity: 97.83954,
        name: 'Dash',
        krakenKey: 'DASH',
        fractionSize: 2
      },
      ETC: {
        quantity: 1769.83335,
        name: 'EthereumClassic',
        krakenKey: 'ETC',
        fractionSize: 2
      },
      XLM: {
        quantity: 0,
        name: 'StellarLumens',
        krakenKey: 'XLM',
        fractionSize: 4
      }
    };

    // vm.currencies.BTC.quantity += 1.40195744;
    // vm.currencies.ETH.quantity += 6.36959781;
    // vm.currencies.ETC.quantity -= (74.49822 + 295.14893570);

    vm.data = {};
    Object.keys(vm.currencies).forEach(function(currency) {
      vm.data[currency] = {};
    });

    vm.updateSummary = function() {
      cryptoService.getSummary(Object.keys(vm.currencies))
        .then(function(result) {
          result.forEach(function(row) {
            vm.data[row.short].coincap = row;
          });
          $timeout(vm.updateSummary, 10000);
        });
    };

    vm.updateKraken = function() {
      krakenService.getSummary()
        .then(function(result) {
          Object.keys(result).forEach(function(key) {
            var key2 = (key === 'XBT') ? 'BTC' : key;
            var increased = false;
            if(vm.data[key2].kraken) {
              increased = vm.data[key2].kraken.b[0] < result[key].b[0];
            }
            vm.data[key2].kraken = result[key];
            vm.data[key2].kraken.increased = increased;
            vm.data[key2].value = result[key].b[0] * vm.currencies[key2].quantity * audExchangeRate;
          });
          vm.computeTotal();
          $timeout(vm.updateKraken, 5000);
        });
    };

    vm.updateSummary();
    vm.updateKraken();

    vm.computeTotal = function() {
      var total = 0;
      angular.forEach(vm.data, function(data) {
        total += data.value;
      });
      vm.sum = total - deposit;
    };

  }
})();


(function() {
  'use strict';

  angular
    .module('geckoboard')
    .service('krakenService', KrakenService);

  KrakenService.$inject = ['$http', '$q'];

  function KrakenService($http, $q) {


    return {
      getSummary: getSummary
    }

    function getSummary() {
      var deferred = $q.defer();
      var promise = $http.get('https://api.kraken.com/0/public/Ticker?pair=DASHUSD,XBTUSD,ETHUSD,ETCUSD,LTCUSD,XRPXBT,XLMUSD');

      promise.then(function(response) {

        var nonUsd = [];
        var finalData = {};

        Object.keys(response.data.result).forEach(function(key) {
          var newKey;
          if(key.indexOf('X') === 0) {
            newKey = key.substring(1,4);
          } else {
            newKey = key.substring(0,4);
          }

          if(key.indexOf('USD') < 0) {
            nonUsd.push({key: key, newKey: newKey});
          }

          finalData[newKey] = response.data.result[key];
        });

        nonUsd.forEach(function(non) {
          var otherKey = non.key.substring(4) + 'ZUSD';
          finalData[non.newKey].a[0] = (parseFloat(response.data.result[non.key].a[0])
            * parseFloat(response.data.result[otherKey].b[0])).toString();
          finalData[non.newKey].b[0] = (parseFloat(response.data.result[non.key].b[0])
            * parseFloat(response.data.result[otherKey].a[0])).toString();
        });

        deferred.resolve(finalData);
      }, function(error) {
        deferred.reject(error);
      });

      return deferred.promise;
    }


  }

}());

(function() {
  'use strict';

  angular
    .module('geckoboard')
    .service('fxService', FxService);

  FxService.$inject = ['$http', '$q'];
  function FxService($http, $q) {

    return {
      convert: convert,
      getRate: getRate
    };

    function getRate(from, to) {
      return convert(1, from, to);
    }

    function convert(amount, from, to) {
      var deferred = $q.defer();
      var promise = $http.get(
        'https://www.google.com/finance/converter?a=' + amount +
        '&from=' + from +
        '&to=' + to);
      promise.then(function(response) {
        deferred.resolve(response.data.match(/<span class=bld>(.*)<\/span>/)[1].replace(/ AUD/g, ''));
      }, function(error) {
        deferred.reject(error);
      });

      return deferred.promise;

    }
  }

})();

(function() {
  'use strict';

  angular
    .module('geckoboard')
    .service('fixerFxService', FixerFxService);

  FixerFxService.$inject = ['$http', '$q'];
  function FixerFxService($http, $q) {

    return {
      getRate: getRate
    };

    function getRate(from, to) {
      var deferred = $q.defer();
      var promise = $http.get('https://api.fixer.io/latest?base=' + from + '&symbols=' + to);
      promise.then(function(response) {
        deferred.resolve(response.data.rates[to]);
      }, function(error) {
        deferred.reject(error);
      });
      return deferred.promise;

    }

  }

})();

(function() {
  'use strict';

  angular
    .module('geckoboard')
    .service('cryptoService', CryptoService);

  CryptoService.$inject = ['$http', '$q'];
  function CryptoService($http, $q) {

  	return {
  		getHistory: getHistory,
      getSummary: getSummary
  	}

  	function getHistory(currency) {
  		return $http.get('https://www.coincap.io/history/1day/' + currency);
  	}

    function getSummary(currencies) {
      var deferred = $q.defer();

      var promise = $http.get('https://www.coincap.io/front');
      promise.then(function(response) {
        var data = response.data.filter(function(row) {
          return currencies.indexOf(row.short) >= 0;
        }).map(function(row) {
          row.increased = parseFloat(row.cap24hrChange) > 0;
          row.rate = (row.increased ? 1 : -1) * parseFloat(row.cap24hrChange);
          return row;
        });
        deferred.resolve(data);
      }, function(error) {
        deferred.reject(error);
      });
      return deferred.promise;
    }
  }

})();

